/* game1.scss */
body.hide-background {
    background-image: none !important;
}

.button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    position: absolute; // 讓按鈕絕對定位
    top: 50%; // 垂直居中
    left: 50%; // 水平居中
    transform: translate(-50%, -50%); // 使按鈕的中心點在畫面中央
    z-index: 100; // 確保按鈕在最上層

    &:hover {
        background-color: #45a049;
    }
}