/* dialog.scss */

/* 背景图片样式 */
.background-image-01,
.background-image-02,
.background-image-03,
.background-image-04,
.background-image-05 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: -1;
}

/* 遮罩层样式，手机设备上不显示 */
.custom-modal-overlay {
    display: none;
}

/* 对话框标题样式，手机设备上不显示 */
.custom-modal-title {
    display: none;
}

/* 对话框主体样式 */
.custom-modal {
    width: 80vw;
    /* 适应屏幕宽度 */
    max-width: 1000px;
    /* 最大宽度限制 */
    height: 30vh;
    /* 固定高度 */
    max-height: 80vh;
    /* 最大高度限制 */
    background-color: white;
    position: fixed;
    bottom: 5rem;
    /* 距离底部的距离 */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: flex-start;
    z-index: 1000;
    border-radius: 10px;
    padding: 20px;
    /* 增加内边距 */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    /* 添加阴影效果 */
}

/* 对话框中的图片样式 */
.custom-modal-image {
    width: 20%;
    /* 图片宽度占对话框宽度的一半 */
    height: auto;
    /* 自适应高度 */
    position: absolute;
    left: -10%;
    /* 从左侧向左偏移50% */
    top: -15%;
    /* 从顶部向上偏移50% */
    transform: scale(1.5);
    /* 放大图片 */
    z-index: 3000;
}

/* 对话框内容样式 */
.custom-modal-content {
    text-align: center;
    font-size: 2rem;
    /* 适应手机屏幕的字体大小 */
    font-weight: bold;
    margin: auto;
    color: darkgreen;
    width: 100%;
    /* 宽度占满对话框 */
    flex-direction: column;
    padding: 0 5.5rem;
    /* 左右内边距增加 */
}

/* 对话框按钮样式 */
.custom-modal-button {
    position: absolute;
    border: 2px solid #2e3730;
    color: #2e3730;
    cursor: pointer;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 6rem;
    height: 3rem;
    font-weight: bold;
    font-size: 1.3rem;
    right: 1rem;
    bottom: 1rem;
}

.custom-modal-button-prev {
    position: absolute;
    border: 2px solid #2e3730;
    color: #2e3730;
    cursor: pointer;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 6rem;
    height: 3rem;
    font-weight: bold;
    font-size: 1.3rem;
    right: 8rem;
    bottom: 1rem;
}

/* 倒计时样式 */
.custom-modal-countdown {
    text-align: center;
    font-size: 2rem;
    color: darkgreen;
    position: absolute;
    bottom: 15px;
    left: 40%;
}

/* 主按钮样式 */
.assemble-button {
    width: 12.5rem;
    height: 6rem;
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1001;
    padding: 10px;
    font-size: 1.5rem;
    font-weight: bold;
    background-color: #1890ff;
    color: white;
    border: none;
    cursor: pointer;
    text-align: center;
}

/* 主按钮悬停状态样式 */
.assemble-button:hover {
    background-color: #1573cb;
}

/* 媒体查询：适应手机设备 */
@media screen and (max-width: 768px) {
    .custom-modal {
        width: 90vw;
        /* 缩小宽度适应手机屏幕 */
        max-width: 100%;
        /* 不超过屏幕宽度 */
        height: 40vh;
        /* 调整高度以适应手机屏幕 */
        max-height: 90vh;
        // /* 不超过屏幕高度 */
        bottom: 1rem;
        // /* 调整底部距离 */
    }

    .custom-modal-image {
        width: 25%;
        /* 图片宽度占对话框宽度的一半 */
        height: auto;
        /* 自适应高度 */
        left: -5%;
        top: -50%;
        transform: none;
        /* 恢复原始比例 */
    }

    .custom-modal-content {
        font-size: 1.5rem;
        /* 调整字体大小 */
        padding: 0 5rem;
        /* 缩小左右内边距 */
    }

    .custom-modal-button {
        width: 5rem;
        height: 2.5rem;
        font-size: 1rem;
        right: 0.5rem;
        bottom: 0.5rem;
    }

    .custom-modal-button-prev {
        width: 5rem;
        height: 2.5rem;
        font-size: 1rem;
        right: 6rem;
        bottom: 0.5rem;
    }

    .assemble-button {
        width: 10rem;
        /* 缩小按钮宽度 */
        height: 5rem;
        /* 缩小按钮高度 */
        font-size: 1.2rem;
        /* 缩小按钮字体大小 */
    }
}