*{margin:0;padding:0;box-sizing:border-box}html,body{width:100%;height:100%;margin:0;padding:0;overflow:hidden}body{background-image:url(./assets/picture/BG/背景-活動中心.png);background-size:cover;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column;overflow:hidden}.container,.centered-container{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center;width:100%;height:100%}.dialog{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:100%;position:relative}.centered-image{max-width:80%;max-height:80%;margin:auto}.foundButton{height:3rem;padding:15px 30px;border-radius:10px;background:#d5f365;border:none;font-family:inherit;color:#2e3730;font-weight:bold;font-size:1.5rem;cursor:pointer;position:absolute;bottom:1rem;left:50%;transform:translateX(-50%);display:flex;justify-content:center;align-items:center}.foundButton:hover{background-color:#c4e056}@media screen and (max-width: 768px){html{transform:none;transform-origin:center center;overflow:hidden}.foundButton{padding:10px 20px;font-size:1rem}.centered-image{max-width:20rem;max-height:20rem;margin:auto;bottom:3rem}}@media screen and (orientation: portrait){html,body{transform:none;width:100%;height:100%}}