.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.mapContainer {
    position: relative;
}

.mapImage {
    width: 40%;
    /* 地圖圖片占畫面40% */
    max-width: 600px;
    /* 最大寬度600px */
    height: auto;
}

.toggleButtonContainer {
    margin-top: 20px;
}

.buttonsContainer {
    display: flex;
    flex-direction: row;
    /* 水平排列 */
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    gap: 10px;
    /* 按鈕之間的間距 */
}

.map-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    /* 防止按鈕文字換行 */
    transition: background-color 0.3s ease;

    &.audioPlayed {
        background-color: #a5d6a7;
        cursor: not-allowed;
    }

    &:hover {
        background-color: #45a049;
    }
}

.audio-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    white-space: nowrap;
    /* 防止按鈕文字換行 */
    transition: background-color 0.3s ease;

    &.audioPlayed {
        background-color: #a5d6a7;
        cursor: not-allowed;
    }

    &:hover {
        background-color: #45a049;
    }
}

.resultContainer {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 80%;
    max-width: 300px;
}